html {
  font-family: "Bebas Neue Pro", sans-serif;
  line-height: 1.42;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  color: #FFF;
  font-family: "Bebas Neue Pro", sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 1.42;
  background: #FFF;
  scroll-behavior: smooth;
}
@media (max-width: 1259px) {
  body {
    font-size: 16px;
    font-size: 1rem;
  }
}
@media (max-width: 599px) {
  body {
    font-size: 14px;
    font-size: 0.875rem;
  }
}
@media (min-width: 1921px) {
  body {
    font-size: 21px;
    font-size: 1.3125rem;
  }
}

input::-ms-clear {
  display: none;
}

/*  Safari */
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

p {
  margin: 0px 0px 18px;
}

ul, ol {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
  font-family: "Bebas Neue Pro";
  font-weight: 500;
  margin: 40px 0px 40px;
  text-transform: none;
  line-height: 1.2;
}
