  h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
    margin: 24px 0 16px;
  }
}

h1, .h1 {
  font-size: 100px;
  font-size: 6.25rem;
  margin-top: 0;
  line-height: 1.15;
}
@media (max-width: 1680px) {
  h1, .h1 {
    font-size: 86px;
    font-size: 5.375rem;
  }
}
