  h1, .h1 {
    font-size: 52px;
    font-size: 3.25rem;
    line-height: 1.1;
  }
}
@media (max-width: 768px) {
  h1, .h1 {
    font-size: 54px;
    font-size: 3.375rem;
    line-height: 1.1;
  }
}
@media (max-width: 599px) {
  h1, .h1 {
    font-size: 42px;
    font-size: 2.625rem;
    line-height: 1.14;
  }
}

h2, .h2, .ui-h2 {
  font-size: 54px;
  font-size: 3.375rem;
  line-height: 1.2;
}
@media (max-width: 959px) {
  h2, .h2, .ui-h2 {
    font-size: 40px;
    font-size: 2.5rem;
  }
}
@media (max-width: 599px) {
  h2, .h2, .ui-h2 {
    font-size: 32px;
    font-size: 2rem;
  }
}

h3, .h3, .ui-h3 {
  font-size: 40px;
  font-size: 2.5rem;
  line-height: 1.2;
}
@media (max-width: 959px) {
  h3, .h3, .ui-h3 {
    font-size: 32px;
    font-size: 2rem;
  }
}
@media (max-width: 599px) {
  h3, .h3, .ui-h3 {
    font-size: 26px;
    font-size: 1.625rem;
  }
}

h4, .h4, .ui-h4 {
  font-size: 30px;
  font-size: 1.875rem;
}

h5, .h5 {
  font-size: 24px;
  font-size: 1.5rem;
}

h6, .h6, .ui-h5 {
  font-size: 18px;
  font-size: 1.125rem;
  text-transform: none;
  font-weight: 700;
}

.ui-font-midi {
  font-size: 16px;
  font-size: 1rem;
}

.ui-font-mini {
  font-size: 14px;
  font-size: 0.875rem;
}

.h20 {
  font-size: 20px;
  font-size: 1.25rem;
}

.ui-strong,
.ui-bold {
  font-weight: 700;
}

.ui-demi {
  font-weight: 600;
}

a {
  color: #FFF8D9;
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

