  }

  ::-webkit-scrollbar-thumb:hover {
    background: #33503e;
  }
}
.ui-btn {
  display: inline-block;
  vertical-align: middle;
  height: 71px;
  line-height: 71px;
  max-width: 100%;
  position: relative;
  border: none;
  border-radius: 10px;
  padding: 0px 60px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: #FFF;
  color: #23372B;
  text-align: center;
  font-size: 22px;
  font-size: 1.375rem;
  letter-spacing: 1.6px;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  transition: box-shadow 0.2s, transform 0.2s;
  box-shadow: 14px 14px 0px #5E6738;
}
.ui-btn.ui-btn-colored {
  color: #FFF;
  background-color: #C74403;
  box-shadow: 0px 0px 0px 0px rgba(20, 31, 24, 0.05), 4px 2px 10px 0px rgba(20, 31, 24, 0.05), 16px 9px 18px 0px rgba(20, 31, 24, 0.04), 36px 19px 24px 0px rgba(20, 31, 24, 0.03), 64px 34px 29px 0px rgba(20, 31, 24, 0.01), 99px 54px 32px 0px rgba(20, 31, 24, 0), 14px 14px 0px 0px #DECA99;
}
@media (max-width: 1680px) {
  .ui-btn.ui-btn-colored {
    box-shadow: 0px 0px 0px 0px rgba(20, 31, 24, 0.05), 4px 2px 10px 0px rgba(20, 31, 24, 0.05), 16px 9px 18px 0px rgba(20, 31, 24, 0.04), 36px 19px 24px 0px rgba(20, 31, 24, 0.03), 64px 34px 29px 0px rgba(20, 31, 24, 0.01), 99px 54px 32px 0px rgba(20, 31, 24, 0), 10px 10px 0px 0px #DECA99;
  }
}
@media (max-width: 1140px) {
  .ui-btn.ui-btn-colored {
    box-shadow: 0px 0px 0px 0px rgba(20, 31, 24, 0.05), 4px 2px 10px 0px rgba(20, 31, 24, 0.05), 16px 9px 18px 0px rgba(20, 31, 24, 0.04), 36px 19px 24px 0px rgba(20, 31, 24, 0.03), 64px 34px 29px 0px rgba(20, 31, 24, 0.01), 99px 54px 32px 0px rgba(20, 31, 24, 0), 7px 7px 0px 0px #DECA99;
  }
}
.ui-btn.ui-btn-dark {
  color: #FFF;
  background-color: #23372B;
  box-shadow: 14px 14px 0px 0px #5E6738;
  text-overflow: ellipsis;
}
@media (max-width: 1680px) {
  .ui-btn.ui-btn-dark {
    box-shadow: 10px 10px 0px 0px #5E6738;
  }
}
@media (max-width: 1140px) {
  .ui-btn.ui-btn-dark {
    box-shadow: 7px 7px 0px 0px #5E6738;
  }
}
@media (max-width: 768px) {
  .ui-btn {
    box-shadow: 10px 10px 0px #5E6738;
  }
}
.ui-btn svg {
  transform: translate(0, 20%);
  margin-right: 8px;
}
@media (max-width: 1680px) {
  .ui-btn svg {
    width: 20px;
    height: 20px;
  }
}
@media (max-width: 1140px) {
  .ui-btn svg {
    width: 16px;
    height: 16px;
  }
}
@media (max-width: 599px) {
  .ui-btn svg {
    width: 20px;
    height: 20px;
  }
}
.ui-btn:hover {
  box-shadow: none;
}
@media (max-width: 1680px) {
  .ui-btn {
    font-size: 18px;
    font-size: 1.125rem;
    height: 60px;
    line-height: 60px;
    padding: 0px 46px;
    box-shadow: 10px 10px 0px #5E6738;
  }
}
@media (max-width: 1140px) {
  .ui-btn {
